import React from 'react';
import {authAPI, securityAPI} from "../api/api";
import {stopSubmit} from "redux-form";

const SET_USER_DATA = 'SET_USER_DATA';
export const setUserData = (id, email, login, isAuth) => {
    return {
        type: SET_USER_DATA,
        payload: {
            id, email, login, isAuth
        }
    }
}

const IS_LOADING = 'IS_LOADING';
export const isLoading = (isLoadingStatus) => {
    return {
        type: IS_LOADING,
        isLoadingStatus
    }
}
const RECAPCHA_URL = 'RECAPCHA_URL';
export const getRecapcha = (url) => {

    return {
        type: RECAPCHA_URL,
        url
    }
}

let initialState = {
    userId: null,
    email: null,
    login: null,
    isAuth: false,
    isLoadingStatus: false,
    recaptchaUrl: null

}
let authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DATA:
            return {
                ...state,
                ...action.payload,
                isAuth: action.payload.isAuth
            }

        case IS_LOADING:
            return {
                ...state,
                isLoadingStatus: action.isLoadingStatus
            }
        case RECAPCHA_URL:
            return {
                ...state,
                recaptchaUrl: action.url
            }
        default :
            return state;
    }
}

export const authMe = () => {
    return async (dispatch) => {
        let response = await authAPI.auth();
        if (response.resultCode === 0) {                //user eger saytda qeydiyyatdan kecibse sifir qaytarir
            let {id, login, email} = response.data;   // datani parchalayiriq
            dispatch(setUserData(id, email, login, true)); // datani gonderirik yeni state kimi
        }
    }
}
const getRecapchaUrl = () => {
    return async (dispatch) => {
        let recapchaUrl = await securityAPI.getRecapchaUrl();
         dispatch(getRecapcha(recapchaUrl));
    }
}

export const loginMe = (email, password, rememberMe, recapchaUrl) => {
    return async (dispatch) => {
        let response = await authAPI.login(email, password, rememberMe, recapchaUrl);
        if (response.resultCode === 0) {
            dispatch(authMe());
        } else {
            if (response.resultCode === 10) {
                dispatch(getRecapchaUrl());
            }
            let errorMessage = response.messages.map(m => <p>{m}</p>);
            dispatch(stopSubmit('login', {_error: [errorMessage]}))
        }
    }
}
export const logOutMe = () => {
    return async (dispatch) => {
        let response = await authAPI.logOut();
        if (response.resultCode === 0) {
            dispatch(setUserData(null, null, null, false))
        }
    }
}
export default authReducer;
