import React from 'react';
import './App.css';
import Navbar from './components/Navbar/Navbar';
import {Redirect, Route, Switch, withRouter} from 'react-router-dom';
import News from "./components/News/News";
import Music from "./components/Music/Music";
import Settings from "./components/Settings/Settings";
import ProfileContainer from "./components/Profile/ProfileContainer";
import HeaderContainer from "./components/Header/HeaderContainer";
import LoginPage from "./components/Login/Login";
import {connect} from "react-redux";
import {compose} from "redux";
import {initialiseAPP, setNavbarToggleStatus} from "./redux/appReducer";
import ShowLoadingAnimation from "./components/common/ShowLoadingAnimation/ShowLoadingAnimation";
import {WithSuspense} from "./hoc/WithSuspense";
import Not_found_page from "./components/common/Not_found_page";
import NavbarSmallDevice from "./components/Navbar/NavbarSmallDevice";
import {Default, Mobile, Mobile_768} from "./components/common/Responsive/Responsive";
// import
const UsersContainer = React.lazy(() => import('./components/Friends/UsersContainer'));
const DialogsContainer = React.lazy(() => import("./components/Dialogs/DialogsContainer"));


class App extends React.Component {
    // state={
    //     navbarToggle:this.props.navbarToggle
    // }

    componentDidMount() {
        this.props.initialiseAPP();    //check User logged
    }
    componentDidUpdate(prevProps, prevState, snapshot) {
        // debugger
        if(prevProps.navbarToggle!=this.props.navbarToggle){
            this.props.initialiseAPP();
        }
    }

    render() {
        if (!this.props.initialised) {
            return <ShowLoadingAnimation/>
        }
        return (<div className='app_wrapper'>
                <HeaderContainer/>
                {/*resolution switch*/}
                {/*case1*/}
                <Default>
                    <Navbar/>
                </Default>
                {/*case2*/}
                <Mobile>
                    {this.props.navbarToggle ? <NavbarSmallDevice
                        navbarToggle={this.props.navbarToggle}
                        setNavbarToggleStatus={this.props.setNavbarToggleStatus}
                    />:''}
                </Mobile>
                {/*resolution switch*/}
                <div className='app_wrapper_content'>
                    <Switch>
                        <Route path='/profile/:userId?'
                               render={() => <ProfileContainer/>}/>
                        <Route path='/dialogs' render={WithSuspense(DialogsContainer)}/>
                        <Route path='/news' component={News}/>
                        <Route path='/music' component={Music}/>
                        <Route path='/settings' component={Settings}/>
                        <Route path='/friends' render={WithSuspense(UsersContainer)}/>
                        <Route path='/login' render={() => <LoginPage/>}/>
                        <Route exact path='/' render={() => <Redirect to='/profile'/>}/>
                        <Route path='*' render={() =><Not_found_page/>}/>
                    </Switch>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    initialised: state.app.initialised,
    navbarToggle: state.app.navbarToggle
})
export default compose(
    withRouter,
    connect(mapStateToProps, {initialiseAPP,setNavbarToggleStatus}))
(App);


