import React, {useState} from 'react';
import h from './Header.module.css';
import {NavLink} from "react-router-dom";
// import logo from "./../assets/blog-logo.png"
import {AiOutlineLogout} from "react-icons/ai"
import {DiSenchatouch} from "react-icons/di"
import {TiThMenu} from "react-icons/ti";
import {Default, Desktop, Mobile, MobileSmall, Tablet} from "../common/Responsive/Responsive";

const Header = ({isAuth,login,logOutMe,navbarToggle,setNavbarToggleStatus}) => {
    // console.log(navbarToggle)
    let logoBrandText=(text='')=>(
        <h2 className={h.logoBrandH2}><DiSenchatouch/>{text}</h2>
    )

    return (
        <header className={h.header}>
           <div className={h.wrapper}>
               {/*<img className={h.logoBrand} src={logo} alt=''/>*/}
               <div className={h.wrapper_logo}>
                   <Desktop>
                       {logoBrandText('Blog Site')}
                   </Desktop>
                   <Tablet>
                       {logoBrandText('Blog Site')}
                   </Tablet>
                   <Mobile>
                       {logoBrandText('Blog Site')}
                   </Mobile>
                   <MobileSmall>
                       {logoBrandText('')}
                   </MobileSmall>
               </div>
               <div className={h.wrapper_spacer}>
                   <Mobile>
                   <span onClick={
                       ()=>{navbarToggle?setNavbarToggleStatus(false):
                           setNavbarToggleStatus(true)}}
                          className={h.burger_menu}>
                       <TiThMenu/>
                   </span>
                   </Mobile>
               </div>
               <div className={h.wrapper_loginBlock}>
                   {isAuth?<div className={h.userProfile}>
                       <label>
                           <span>{login}</span>
                           <span className={h.logOut} onClick={logOutMe}>
                           <AiOutlineLogout/>
                           </span>
                       </label>

                   </div>:<NavLink to={'/login'}>Login</NavLink>}
               </div>
           </div>
        </header>
    );
}
export default Header;
