import React from 'react';
import {NavLink} from 'react-router-dom';
import n from './NavbarSmallDevice.module.css';

const NavbarSmallDevice = ({navbarToggle, setNavbarToggleStatus}) => {
    // const navbarToggle=props.navbarToggle;
    //setNavbarToggleStatus
    console.log(navbarToggle)
    const changeNavbarToggle = () => {
        // debugger
        setNavbarToggleStatus(false);
    }
    return (
        <nav className={n.nav}>
            <div className={`${n.item}`}>
                <NavLink to='/profile'
                         activeClassName={n.active}
                         onClick={changeNavbarToggle}
                    // onBlur={changeNavbarToggle}

                >Profile</NavLink>
            </div>
            <div className={n.item}>
                <NavLink to='/dialogs' activeClassName={n.active}
                         onClick={changeNavbarToggle}
                >Messages</NavLink>
            </div>
            <div className={n.item}>
                <NavLink to='/news' activeClassName={n.active}
                         onClick={changeNavbarToggle}
                >News</NavLink>
            </div>
            <div className={n.item}>
                <NavLink to='/music' activeClassName={n.active}
                         onClick={changeNavbarToggle}
                >Music</NavLink>
            </div>
            <div className={`${n.item} ${n.setting}`}>
                <NavLink to='/settings' activeClassName={n.active}
                         onClick={changeNavbarToggle}
                >Settings</NavLink>
            </div>
            <div className={`${n.item} ${n.friends}`}>
                <NavLink to='/friends' activeClassName={n.active}
                         onClick={changeNavbarToggle}
                >Users</NavLink>
            </div>
        </nav>
    );
}
export default NavbarSmallDevice;
